.trackerContainer {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: minmax(265px, max-content);
      grid-template-columns: minmax(265px, -webkit-max-content);
      grid-template-columns: minmax(265px, max-content);
  row-gap: 10px;
  margin: 10px;
}

.trackerContainer .trackedClassContainer {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: [turn-complete] max-content [info] 1fr [initiative] max-content [move-buttons] max-content [delete] max-content;
      grid-template-columns: [turn-complete] -webkit-max-content [info] 1fr [initiative] -webkit-max-content [move-buttons] -webkit-max-content [delete] -webkit-max-content;
      grid-template-columns: [turn-complete] max-content [info] 1fr [initiative] max-content [move-buttons] max-content [delete] max-content;
  place-items: center;
}

.trackerContainer .roundActionsOuter {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  gap: 10px;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

.trackerContainer .roundActionsOuter .fa-play {
  color: green;
}

.trackerContainer .roundActionsOuter .fa-rotate {
  color: steelblue;
}

.trackerContainer .noFiguresPlaceholder {
  text-align: center;
  font-size: large;
  font-weight: bold;
}

.trackerContainer .noFiguresPlaceholder .fa-triangle-exclamation {
  color: #e6e20e;
  padding-right: 6px;
  -webkit-text-stroke: 1px black;
}

.trackerContainer .separator {
  grid-column: 1/-1;
  height: 8px;
}
